@import 'vars';
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
body {
    background: $clr-bg;
    background-repeat: no-repeat;
    font-family: 'Bebas Neue', cursive;
}
.header {
    color: white;
    font-family: 'Bebas Neue', cursive;
}
.hidden
{display : none;}
.main {
    
    font-family: 'Bebas Neue', cursive;
    &__article {
        margin-top: 75px;
        .card {
            
            position: relative;
            padding-top: 50px;
            min-height: 100%;
            transition: transform .2s ease-in-out;
            &:hover {
                transform: scale(1.04);
            }
            h2 {
                margin-top: 50px;
                text-align: center;
            }
            figure {
                position: absolute;
                user-select: none;
                top: 0;
                left: 50%;
                transform: translate(-50%, -50%);
                img {
                    border-radius: .3rem;
                }
            }
            p {
                margin-bottom: 3.5rem;
            }
            button {
                position: absolute;
                bottom: 1rem;
                right: 1rem;
            }
        }
    }
}

#moreInfoModal {
    legend {
      font-family: 'Bebas Neue', cursive;
        display: flex;
        justify-content: center;
        img {
            border-radius: .3rem;
        }
    }
}
#newCharacterModal {
    #previewFigure {
      font-family: 'Bebas Neue', cursive;
        margin-top: 1rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        img {
            border-radius: .3rem;
        }
        p {
            margin-top: 1rem;
            font-size: .7rem;
            flex: 1 0 100%;
        }
    }
}
#editingModal {
  font-family: 'Bebas Neue', cursive;
    #modifyingPreviewFigure {
        margin-top: 1rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        img {
            border-radius: .3rem;
        }
        p {
            margin-top: 1rem;
            font-size: .7rem;
            flex: 1 0 100%;
        }
    }
}

#shadowBox {
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.2);
    /* Black w/opacity/see-through */
    border: 3px solid;
}

.footer, .header__title {
    text-align: center;
    text-decoration: none;
    font-family: 'Bebas Neue', cursive;
    font-size: 60px;
    letter-spacing: 10px;
    animation: colorRotate 6s linear 0s infinite;
}

@keyframes colorRotate {
    from {
        color: #6666ff;
    }
    10% {
        color: #0099ff;
    }
    50% {
        color: #00ff00;
    }
    75% {
        color: #ff3399;
    }
    100% {
        color: #6666ff;
    }
}
#searchBar {
  
    
    width: 255px;
    height: 32px;
    border-radius: 0.3em;
    border: 1px solid #eaeaea;
    padding: 5px 10px;
    font-size: 12px;
}

#searchWrapper {
    position: relative;
}

#searchWrapper::after {
    content: 'üîç';
    position: absolute;
    top: 7px;
    right: 15px;
}

//
$color: #2194E0;

@keyframes sheen {
  0% {
    transform: skewY(-45deg) translateX(0);
  }
  100% {
    transform: skewY(-45deg) translateX(12.5em);
  }
}

.wrapper {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.button {
  padding: 0.75em 2em;
  margin-bottom: 10px;
  text-align: center;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.466);
  border: 2px solid $color;
  font-size: 24px;
  display: inline-block;
  border-radius: 0.3em;
  transition: all 0.2s ease-in-out;
  position: relative;
  overflow: hidden;
  &:before {
    content: "";
    background-color: rgba(255,255,255,0.5);
    height: 100%;
    width: 3em;
    display: block;
    position: absolute;
    top: 0;
    left: -4.5em;
    transform: skewX(-45deg) translateX(0);
    transition: none;
  }
  &:hover {
    background-color: $color;
    color: rgb(255, 255, 255);
    border-bottom: 4px solid darken($color, 10%);
    &:before {
      transform: skewX(-45deg) translateX(13.5em);
     transition: all 0.5s ease-in-out;
    }
  }
}
